import axios from 'axios';
import { useState, useEffect } from 'react';

const useFantomPrice = () => {
    const [price, setPrice] = useState(0);
    // const [marketCap, setMarketCap] = useState(0);
    const [priceChange, setPriceChange] = useState(0);

    useEffect(() => {
        getPrice()
        const interval = setInterval(() => {
            getPrice()
        }, 10000)
        return () => {
            clearInterval(interval);
        }
    }, [])
    return { price, priceChange };
    
    async function getPrice() {
        const { data } = await axios('https://ultrasound.money/api/v2/fees/eth-price-stats');
        setPrice(data.usd);
        setPriceChange(data.h24Change);
    }
}

async function useETHPricef() {
    const { data } = await axios('https://ultrasound.money/api/v2/fees/eth-price-stats');
    return { price: data.usd, priceChange: data.h24Change }
}

export {useETHPricef}
export default useFantomPrice;