import axios from 'axios';
import { useState, useEffect } from 'react';

const useBALDPrice = () => {
    const [price, setPrice] = useState(0);

    useEffect(() => {
        getPrice()
        const interval = setInterval(() => {
            getPrice()
        }, 10000)
        return () => {
            clearInterval(interval);
        }
    }, [])
    return { price };
    
    async function getPrice() {
        try {
            const { data } = await axios('https://api.dexscreener.com/latest/dex/pairs/base/0xE96df8F5ef1A8790415068c798765b07D57643bd');
            setPrice(data?.pairs?.priceUsd || 0.05);
        } catch {
            setPrice(0.05)
        }
    }
}

async function useBALDPricef() {
    try {
        const { data } = await axios('https://api.dexscreener.com/latest/dex/pairs/base/0xE96df8F5ef1A8790415068c798765b07D57643bd');
        return data?.pairs?.priceUsd || 0.05
    } catch {
        return 0.05
    }
}

export {useBALDPricef};
export default useBALDPrice;