import axios from 'axios'
import { useState, useEffect } from 'react'
import Web3 from 'web3'
import ERC20ABI from '../tomb-finance/ERC20.abi.json'

const web3 = new Web3('https://developer-access-mainnet.base.org')
const SHARE = new web3.eth.Contract(ERC20ABI, "0x3FF13895C5449591e3296192649169c9110941F0")

const useSHAREPrice = () => {
    const [SHAREPriceETH, setPriceETH] = useState(0)
    const [SHAREPrice, setPrice] = useState(0)
    const [SHAREMarketCap, setMarketCap] = useState(0)
    const [SHARELiquidity, setLiquidity] = useState(0)
    const [SHARESupply, setSupply] = useState(0)
    

    useEffect(() => {
        getPrice()
        const interval = setInterval(() => {
            getPrice()
        }, 10000)
        return () => {
            clearInterval(interval);
        }
    }, [])
    return { SHAREPriceETH, SHAREPrice, SHAREMarketCap, SHARELiquidity, SHARESupply };
    
    async function getPrice() {
        const { data } = await axios('https://api.dexscreener.com/latest/dex/pairs/base/0x847ae7a90C5D509F9F68e76641f20B70615BAf74')
        const price = data.pairs[0].priceUsd
        const priceETH = data.pairs[0].priceNative
        const liquidity = data.pairs[0].liquidity.usd
        const supply = await SHARE.methods.totalSupply().call()
        const formattedSupply = Number(web3.utils.fromWei(supply)).toFixed(2)
        setPriceETH(priceETH)
        setPrice(price)
        setMarketCap(Number(formattedSupply * price).toFixed(2))
        setLiquidity(liquidity)
        setSupply(formattedSupply)
    }
}

async function useSHAREPricef() {
    const { data } = await axios('https://api.dexscreener.com/latest/dex/pairs/base/0x847ae7a90C5D509F9F68e76641f20B70615BAf74')
    const price = data.pairs[0].priceUsd
    const priceETH = data.pairs[0].priceNative
    const liquidity = data.pairs[0].liquidity.usd
    const supply = await SHARE.methods.totalSupply().call()
    const formattedSupply = Number(web3.utils.fromWei(supply)).toFixed(2)

    return { SHAREPriceETH: priceETH, SHAREPrice: price, SHAREMarketCap: Number(formattedSupply * price).toFixed(2), SHARELiquidity: liquidity, SHARESupply: formattedSupply };
}

export {useSHAREPricef}
export default useSHAREPrice;